.overlay {

    opacity: 0;
    visibility: hidden;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 20;
    transition: .3s all;
}


.modal {

    opacity: 0;
    visibility: hidden;

    width: 100%;
    height: 100vh;

    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 30;

    box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
    padding: 30px;
    border-radius: 3px;
    background-color: rgba(17, 21, 24, 0.9);
    transition: 0.3s all;
}



.modal.active,
.overlay.active {
    opacity: 1;
    visibility: visible;
}



.modal__cross {
    width: 15px;
    height: 15px;
    position: absolute;
    top: 20px;
    right: 20px;
    fill: #444;
    cursor: pointer;
    background-color: transparent;
    border: none;
}




.modal-link {
    cursor: pointer;
    background-color: transparent;
    border: none;
    padding: 0;
}

@media screen and (max-width: 767px) {
    .modal {
        text-align: start;
    }
}